import { useState } from 'react';
import { CloseButton } from 'UI/CloseButton';
import styles from './CreateGos.module.css';
import axios from 'api/axios';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import {
  TextField,
  Button,
  FormControl,
  FormControlLabel,
  Radio,
  RadioGroup,
  FormLabel,
  Checkbox,
} from '@mui/material';
import dayjs from 'dayjs';

import 'dayjs/locale/ru';
import { OptionsList } from 'components/Helpers/OptionsList';
import { ContractsGovList } from '../ContractsGovList';
import { CheckBox } from '@mui/icons-material';

const CreateGos = props => {
  const [formValue, setformValue] = useState({
    contr_id: props.handelrdata?.contracts_gov?.contr_id || '',
    contr_num: props.handelrdata?.contracts_gov?.contr_num || '',
    contr_date: props.handelrdata?.contracts_gov?.contr_date || '',
    contr_fin_date: props.handelrdata?.contracts_gov?.contr_fin_date || '',
    spec_demand: props.handelrdata?.contracts_gov?.spec_demand || 0,
    contr_descr: props.handelrdata?.contracts_gov?.contr_descr || '',
  });

  function isValidDate(d) {
    if (d === null) {
      d = '';
    }
    d = new Date(d);
    if (d instanceof Date && !isNaN(d)) {
      return dayjs(d);
    } else {
      return '';
    }
  }

  const submitHandler = async e => {
    e.preventDefault();
    try {
      // const ComplaintFormData = new FormData();
      // ComplaintFormData.append('contr_num', formValue.contr_num);
      // ComplaintFormData.append('contr_date', formValue.contr_date);
      // ComplaintFormData.append('contr_fin_date', formValue.contr_fin_date);
      // ComplaintFormData.append('spec_demand', formValue.spec_demand);
      // ComplaintFormData.append('contr_descr', formValue.contr_descr);
      console.log('formValue', formValue);
      props.onCancel();
      await axios({
        method: 'post',
        url: `incomeblocks${props.dataName.link}`,
        data: formValue,
        withCredentials: true,
      });
    } catch (error) {
      console.log(error);
    }
  };

  const handleChange = event => {
    console.log(event.target.id);
    if (event.target.id === 'checkbox') {
      if (event.target.checked === true) {
        setformValue({
          ...formValue,
          [event.target.name]: '1',
        });
      } else {
        setformValue({
          ...formValue,
          [event.target.name]: '0',
        });
      }
    } else {
      setformValue({
        ...formValue,
        [event.target.name]: event.target.value,
      });
    }
  };

  const pickedhandler = event => {
    setformValue({
      ...formValue,
      [event.name]: event.number,
    });
  };

  return (
    <>
      <div className={styles.backdrop} onClick={props.onCancel} />
      <form className={styles.modal} onSubmit={submitHandler}>
        <div className={styles.formaData}>
          <div className={styles.rowSave}>
            <div className={styles.formaDataRow}>
              <Button
                variant="contained"
                color="primary"
                type="submit"
                value="Сохранить"
              >
                Сохранить
              </Button>
              <div className={styles.rowbuttonfonts}>
                {props.dataName.namedata}
              </div>
            </div>
            <CloseButton close={props.onCancel} />
          </div>
          <div className={styles.formaDataRow}>
            <div className={styles.formaDataList}>
              <FormControl sx={{ minWidth: 270 }}>
                <LocalizationProvider dateAdapter={AdapterDayjs}>
                  <DatePicker
                    slotProps={{
                      textField: { required: true, error: false },
                    }}
                    format="DD/MM/YYYY"
                    defaultValue={
                      isValidDate(
                        props.handelrdata?.contracts_gov.contr_date
                      ) || null
                    }
                    onChange={e =>
                      setformValue({
                        ...formValue,
                        contr_date: `${e.$y}.${e.$M + 1}.${e.$D}`,
                      })
                    }
                    name="contr_date"
                    label="Дата госконтракта"
                  />
                </LocalizationProvider>
              </FormControl>
            </div>
            <div className={styles.formaDataList}>
              <FormControl sx={{ minWidth: 270 }} disabled>
                <TextField
                  required={true}
                  name="contr_num"
                  onChange={handleChange}
                  defaultValue={
                    props.handelrdata?.contracts_gov.contr_num || ''
                  }
                  label="Номер госконтракта"
                ></TextField>
              </FormControl>
            </div>
            <div className={styles.formaDataList}>
              <FormControlLabel
                sx={{ mx: 0, minWidth: 270 }}
                value="top"
                control={
                  <Checkbox
                    defaultChecked={Boolean(
                      parseInt(props.handelrdata?.contracts_gov?.spec_demand)
                    )}
                    id="checkbox"
                    name="spec_demand"
                    onChange={handleChange}
                  />
                }
                label="Особо востребован"
                aria-label="position"
                labelPlacement="top"
              ></FormControlLabel>
            </div>
            <div className={styles.formaDataRow}>
              <div className={styles.formaDataList}>
                <FormControl sx={{ minWidth: 270 }}>
                  <LocalizationProvider dateAdapter={AdapterDayjs}>
                    <DatePicker
                      slotProps={{
                        textField: { required: false, error: false },
                      }}
                      format="DD/MM/YYYY"
                      defaultValue={
                        isValidDate(
                          props.handelrdata?.contracts_gov.contr_fin_date
                        ) || null
                      }
                      onChange={e =>
                        setformValue({
                          ...formValue,
                          contr_fin_date: `${e.$y}.${e.$M + 1}.${e.$D}`,
                        })
                      }
                      name="contr_fin_date"
                      label="Дата завершения"
                    />
                  </LocalizationProvider>
                </FormControl>
              </div>
            </div>

            <div className={styles.formaDataRow}>
              <div className={styles.formaDataList}>
                <FormControl sx={{ minWidth: '72ch' }} disabled>
                  <TextField
                    label="Примечания"
                    defaultValue={
                      props.handelrdata?.contracts_gov.contr_descr || ''
                    }
                    onChange={handleChange}
                    name="contr_descr"
                    rows={3}
                    multiline
                  ></TextField>
                </FormControl>
              </div>
            </div>
          </div>
        </div>
      </form>
    </>
  );
};

export { CreateGos };
