import { useEffect, useState } from 'react';

import {
  Button,
  TablePagination,
  Paper,
  Table,
  TableCell,
  TableBody,
  TableHead,
  TableRow,
  TableContainer,
  CircularProgress,
  Box,
  Menu,
  IconButton,
} from '@mui/material';
import axios from 'api/axios';

import styles from './GosContracts.module.css';

import { DeleteForever } from '@mui/icons-material';
import { Edit } from '@mui/icons-material';
import { DeleteData } from './DeleteData';

import StarBorder from '@mui/icons-material/StarBorder';
import MenuItem from '@mui/material/MenuItem';
import EditIcon from '@mui/icons-material/Edit';
import ArchiveIcon from '@mui/icons-material/Archive';
import CurrencyRuble from '@mui/icons-material/CurrencyRuble';
import ReceiptLong from '@mui/icons-material/ReceiptLong';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import { useNavigate } from 'react-router-dom';
import { styled, alpha } from '@mui/material/styles';
import { CreateGos } from './CreateGos/CreateGos';

const GosContracts = props => {
  const StyledMenu = styled(props => (
    <Menu
      elevation={0}
      anchorOrigin={{
        vertical: 'bottom',
        horizontal: 'right',
      }}
      transformOrigin={{
        vertical: 'top',
        horizontal: 'right',
      }}
      {...props}
    />
  ))(({ theme }) => ({
    '& .MuiPaper-root': {
      borderRadius: 6,
      marginTop: theme.spacing(1),
      minWidth: 180,
      color:
        theme.palette.mode === 'light'
          ? 'rgb(55, 65, 81)'
          : theme.palette.grey[300],
      boxShadow:
        'rgb(255, 255, 255) 0px 0px 0px 0px, rgba(0, 0, 0, 0.05) 0px 0px 0px 1px, rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px',
      '& .MuiMenu-list': {
        padding: '4px 0',
      },
      '& .MuiMenuItem-root': {
        '& .MuiSvgIcon-root': {
          fontSize: 18,
          color: theme.palette.text.secondary,
          marginRight: theme.spacing(1.5),
        },
        '&:active': {
          backgroundColor: alpha(
            theme.palette.primary.main,
            theme.palette.action.selectedOpacity
          ),
        },
      },
    },
  }));

  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);
  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  function isValidDate(inputDate) {
    const date = new Date(inputDate);
    if (isNaN(date)) {
      return 'Дата не задана';
    }

    const day = date.getDate().toString().padStart(2, '0');
    const mounth = (date.getMonth() + 1).toString().padStart(2, '0');
    const year = date.getFullYear().toString();

    const formate_date = `${day}.${mounth}.${year}`;
    return formate_date;
  }

  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [page, setPage] = useState(0);
  const [showCreateDoc, setShowCreateDoc] = useState(false);
  const [showCreateGos, setShowCreateGoc] = useState(false);
  const [showMainList, setShowMainList] = useState(true);
  const [showDoclist, setShowDoclist] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [selectedReport, SetSelectedReport] = useState([]);
  const [selectedDoc, SetSelectedDoc] = useState([]);
  const [leng, setleng] = useState(0);
  const navigate = useNavigate();
  const [updatingItemId, setUpdatingItemId] = useState(null);
  const [showEditPacDocList, setShowEditPacDocList] = useState(false);
  const [showEditDoc, setShowEditDoc] = useState(false);
  const [createDataName, setCreateDataName] = useState({});
  const [showdelPacDocList, setShowDelPacDocList] = useState(false);
  const [nameField, setnameField] = useState(false);

  useEffect(() => {
    fetchData();
  }, [
    showCreateDoc,
    showCreateGos,
    page,
    rowsPerPage,
    showdelPacDocList,
    showEditDoc,
    showEditPacDocList,
    props.typedoc,
  ]);

  const fetchData = async () => {
    const response = await axios.get(
      `incomeblocks/contractsdoclist?params=${props.typedoc}&page=${
        page + 1
      }&pagesize=${rowsPerPage}`
    );
    const jsonData = await response.data;
    console.log('response', jsonData);
    const { products, len } = jsonData;
    SetSelectedReport(products);
    setleng(len);
    setUpdatingItemId(null);
    setIsLoading(false);
  };

  let pacDocHeader = [
    'Тип',
    'Имя документа',
    'Дата завершения документа',
    'Адресат',
    'Примечания',
  ];

  const handleChangePage = (event, newPage) => {
    setIsLoading(true);
    SetSelectedReport([]);
    setPage(newPage);
  };

  const handleChangeRowsPerPage = event => {
    console.log('handleChangeRowsPerPage', event.target.value);
    setRowsPerPage(+event.target.value);
    setPage(0);
  };

  const StartAddHandler = e => {
    setCreateDataName(e);
    setAnchorEl(null);
    setShowCreateDoc(true);
  };
  const StartAddGos = e => {
    setCreateDataName(e);
    setAnchorEl(null);
    setShowCreateGoc(true);
  };

  const HandleCloseCreateDoc = () => {
    setShowCreateDoc(false);
    setShowCreateGoc(false);
    setShowEditPacDocList(false);
    setShowDelPacDocList(false);
    setShowEditDoc(false);
  };

  const UpdateData = async e => {
    console.log('to UpdateData for blocks', e);
    setShowEditPacDocList(false);
    setUpdatingItemId(selectedDoc);
    try {
      await axios({
        method: 'post',
        url: `incomeblocks/contractsgovupdate`,
        data: e,
        withCredentials: true,
      });
    } catch (error) {
      if (error.response.status === 401) navigate('/login');
    }
    fetchData();
  };



  const HandleEditClick = (e, id) => {
    e.stopPropagation();
    window.scrollTo(0, 0);
    console.log('edit', selectedReport[id]);
    axios
      .get(
        `incomeblocks/${
          selectedReport[id].t.contr_type
            ? 'contractsdocget'
            : 'contractsgovget'
        }?recordid=${selectedReport[id].t.contr_id}`
      )
      .catch(error => {
        if (error.response.status === 401) navigate('/login');
      })
      .then(response => {
        console.log('getresponse', response.data[0][0]);
        if (selectedReport[id].t.contr_type) {
          console.log('doc', response.data[0][0]);
          setnameField({
            namedata: `${
              response.data[0][0].contracts_doc.contr_type == 1
                ? 'Редактирование Договора на получение'
                : 'Редактирование Договора на отгрузку'
            }`,
            link: '/contractsdocupdate',
          });
          SetSelectedDoc(response.data[0][0]);
          setShowEditDoc(true);
        } else {
          setnameField({
            namedata: 'Редактирование Госконтракта',
            link: '/contractsgovupdate',
          });
          SetSelectedDoc(response.data[0][0]);
          setShowEditPacDocList(true);
        }
      });
  };

  const HandleDeleteClick = (e, id) => {
    e.stopPropagation();
    window.scrollTo(0, 0);
    console.log('delt', selectedReport[id]);
    SetSelectedDoc(selectedReport[id]);
    setShowDelPacDocList(true);
  };

  return (
    <>
      {showCreateGos && (
        <CreateGos dataName={createDataName} onCancel={HandleCloseCreateDoc} />
      )}
      {showEditPacDocList && (
        <CreateGos
          dataName={nameField}
          handelrdata={selectedDoc}
          updatedata={UpdateData}
          onCancel={HandleCloseCreateDoc}
        />
      )}


      {showdelPacDocList && (
        <DeleteData
          namedoc={'Документ'}
          todel={selectedDoc}
          onCancel={HandleCloseCreateDoc}
        />
      )}
      <div className={styles.contener}>
        {showMainList && (
          <>
            <Paper
              sx={{
                minWidth: '1300px',
                overflow: 'hidden',
                margin: '15px 0 15px 0',
              }}
            >
              <div className={styles.rowbutton}>
                <div className={styles.button}>
                  <Button
                    sx={{
                      m: 2,
                    }}
                    id="demo-customized-button"
                    aria-controls={open ? 'demo-customized-menu' : undefined}
                    aria-haspopup="true"
                    aria-expanded={open ? 'true' : undefined}
                    variant="contained"
                    disableElevation
                    onClick={handleClick}
                    endIcon={<KeyboardArrowDownIcon />}
                  >
                    Добавить
                  </Button>
                </div>
              </div>
              <StyledMenu
                id="demo-customized-menu"
                MenuListProps={{
                  'aria-labelledby': 'demo-customized-button',
                }}
                anchorEl={anchorEl}
                open={open}
                onClose={handleClose}
              >
                {props.typedoc === 0 && (
                  <MenuItem
                    onClick={event =>
                      StartAddGos({
                        namedata: 'Госконтракт',
                        link: '/contractsgovcreate',
                      })
                    }
                    disableRipple
                  >
                    <EditIcon />
                    Госконтракт
                  </MenuItem>
                )}

                {/* <Divider sx={{ my: 0.5 }} /> */}
                {props.typedoc === 2 && (
                  <MenuItem
                    onClick={event =>
                      StartAddHandler({
                        namedata: 'Договор на отгрузку',
                        link: '/contractsoutcreate',
                      })
                    }
                    disableRipple
                  >
                    <ArchiveIcon />
                    Договор на отгрузку
                  </MenuItem>
                )}
                {props.typedoc === 1 && (
                  <MenuItem
                    onClick={event =>
                      StartAddHandler({
                        namedata: 'Договор на получение',
                        link: '/contractsincreate',
                      })
                    }
                    disableRipple
                  >
                    <ArchiveIcon />
                    Договор на получение
                  </MenuItem>
                )}
              </StyledMenu>

              {/* <div className={styles.rowbutton}>
                <div className={styles.button}>
                  <Button
                    sx={{
                      m: 2,
                    }}
                    variant="contained"
                    color="primary"
                    type="button"
                    value="add"
                    onClick={() => console.log()}
                    onClickCapture={StartAddHandler}
                  >
                    Добавить
                  </Button>
                </div>
                <div className={styles.rowbuttonfonts}>Договор</div>
              </div> */}

              <TablePagination
                count={leng}
                rowsPerPage={rowsPerPage}
                page={page}
                onPageChange={handleChangePage}
                onRowsPerPageChange={handleChangeRowsPerPage}
                rowsPerPageOptions={[10, 25, 100]}
                labelRowsPerPage="Колличество элементов на странице"
                component="div"
                sx={{ padding: 0 }}
              />
              {isLoading ? (
                <div className={styles.center}>
                  <p>Подгружаем данные</p>
                  <Box sx={{ position: 'relative', display: 'inline-flex' }}>
                    <CircularProgress />
                    <Box
                      sx={{
                        top: 0,
                        left: 0,
                        bottom: 0,
                        right: 0,
                        position: 'absolute',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                      }}
                    ></Box>
                  </Box>
                </div>
              ) : (
                <TableContainer>
                  <Table stickyHeader aria-label="sticky table">
                    <TableHead>
                      <TableRow>
                        {pacDocHeader.map((column, id) => (
                          <TableCell key={id}>{column}</TableCell>
                        ))}
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {selectedReport.map((row, id, arr) => {
                        return (
                          <TableRow
                            hover
                            role="checkbox"
                            tabIndex={-1}
                            key={id}
                            id={id}
                          >
                            {updatingItemId?.spechecks_info?.contr_id ===
                            row?.t?.contr_id ? (
                              <>
                                <TableCell></TableCell>
                                <TableCell></TableCell>
                                <TableCell>
                                  {' '}
                                  <div className={styles.center}>
                                    <p>Обновляем данные</p>
                                    <Box
                                      sx={{
                                        position: 'relative',
                                        display: 'inline-flex',
                                      }}
                                    >
                                      <CircularProgress />
                                      <Box
                                        sx={{
                                          top: 0,
                                          left: 0,
                                          bottom: 0,
                                          right: 0,
                                          position: 'absolute',
                                          display: 'flex',
                                          alignItems: 'center',
                                          justifyContent: 'center',
                                        }}
                                      ></Box>
                                    </Box>
                                  </div>
                                </TableCell>
                                <TableCell></TableCell>
                              </>
                            ) : (
                              <>
                                <TableCell>
                                  {(row?.t?.contr_type === 0 &&
                                    row?.t?.spec_demand === 0 && (
                                      <StarBorder
                                        style={{ color: '#6258ff' }}
                                      />
                                    )) ||
                                    (row?.t?.contr_type === 0 &&
                                      row?.t?.spec_demand === 1 && (
                                        <StarBorder
                                          style={{ color: '#f51414' }}
                                        />
                                      )) ||
                                    (row?.t?.contr_type === 1 &&
                                      row?.t?.spec_demand !== 1 && (
                                        <ReceiptLong
                                          style={{ color: '#4cc34a' }}
                                        />
                                      )) ||
                                    (row?.t?.contr_type === 1 &&
                                      row?.t?.spec_demand === 1 && (
                                        <ReceiptLong
                                          style={{ color: '#f51414' }}
                                        />
                                      )) ||
                                    (row?.t?.contr_type === 2 &&
                                      row?.t?.spec_demand !== 1 && (
                                        <CurrencyRuble
                                          style={{ color: '#6258ff' }}
                                        />
                                      )) ||
                                    (row?.t?.contr_type === 2 &&
                                      row?.t?.spec_demand === 1 && (
                                        <CurrencyRuble
                                          style={{ color: '#f51414' }}
                                        />
                                      ))}
                                </TableCell>
                                <TableCell>
                                  {`${
                                    row?.t?.gov_name ? row?.t?.gov_name : ''
                                  }${
                                    row?.t?.contr_name && row?.t?.gov_name
                                      ? '/'
                                      : ''
                                  }${
                                    row?.t?.contr_name ? row?.t?.contr_name : ''
                                  } от ${
                                    row?.t?.contr_date
                                      ? row?.t?.contr_date.slice(
                                          0,
                                          row?.t?.contr_date.length - 14
                                        )
                                      : ''
                                  }`}
                                </TableCell>
                                <TableCell>
                                  {isValidDate(row?.t?.contr_fin_date)}
                                </TableCell>
                                <TableCell>{row?.t?.addr_name}</TableCell>
                                <TableCell>{row?.t?.contr_descr}</TableCell>
                                <TableCell>
                                  <IconButton
                                    edge="end"
                                    sx={{ marginRight: '2px' }}
                                    onClick={event =>
                                      HandleEditClick(event, id)
                                    }
                                  >
                                    <Edit data-value="edit" fontSize="medium" />
                                  </IconButton>
                                  <IconButton
                                    data-value="del"
                                    fontSize="medium"
                                    style={{ color: '#998383' }}
                                    edge="end"
                                    onClick={event =>
                                      HandleDeleteClick(event, id)
                                    }
                                  >
                                    <DeleteForever />
                                  </IconButton>
                                </TableCell>
                              </>
                            )}
                          </TableRow>
                        );
                      })}
                    </TableBody>
                  </Table>
                </TableContainer>
              )}
            </Paper>
          </>
        )}
      </div>
    </>
  );
};

export { GosContracts };
