import axios from "api/axios";
import useAuth from "./useAuth";
import { useNavigate } from "react-router-dom";


const useRefreshToken = () => {
  const navigate = useNavigate();
  const { setAuth } = useAuth();

  const refresh = async () => {
    try {
      const response = await axios.get("/refresh", { withCredentioal: true });

      setAuth((prev) => {
        console.log("prev", JSON.stringify(prev));
        console.log("response", response);
        return {
          ...prev,
          roles: [1],
          firstname: response.data.refreshToken.username,
          accessToken: response.data.refreshToken,
          api_list: response?.data?.refreshToken?.api_list,
        };
      });
      return response.data.refreshToken;
    } catch (error) {
      console.log(error.response.status, "Нет ответа от сервера");
      if (error.response?.status === 401) {
        navigate("login", { replace: true });
      }
    }
  };
  return refresh;
};

export default useRefreshToken;
