import React, { useState } from "react";
import axios from "api/axios";
import {
  TextField,
  Button,
  Container,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
} from "@mui/material";
import { NavBar } from "components/Pages/NavBar";

const OrdersTable = () => {
  const [uniqueCode, setUniqueCode] = useState("");
  const [orders, setOrders] = useState([]);

  const handleFetchOrders = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.get(`api/orderslist/${uniqueCode}`);
      setOrders(response.data);
    } catch (error) {
      console.error("There was an error fetching the orders!", error);
    }
  };

  return (
    <>
      <NavBar link={[]} name={"Заказы"} />
      <Container>
        {/* <input
        type="text"
        className={styles.searchBar}
        // onKeyPress={props.keypressing}
        value={currenElement}
        onChange={currentElementHandler}
        placeholder={props.placeholder}
        onKeyDown={getDaterHandler}
        required
      ></input> */}
        <Typography variant="h4" gutterBottom>
          Список заказов
        </Typography>
        <form onSubmit={handleFetchOrders}>
          <TextField
            label="Промокоды"
            fullWidth
            margin="normal"
            value={uniqueCode}
            onChange={(e) => setUniqueCode(e.target.value)}
            required
          />
          <Button type="submit" variant="contained" color="primary">
            Найти заказы
          </Button>
        </form>
        {orders.length > 0 && (
          <TableContainer component={Paper} style={{ marginTop: "20px" }}>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell>Номер заказа</TableCell>
                  <TableCell>Цена</TableCell>
                  <TableCell>Дата</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {orders.map((order, id) => (
                  <TableRow key={id}>
                    <TableCell>{order.order_number}</TableCell>
                    <TableCell>{order.price}</TableCell>
                    <TableCell>{order.created_at}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        )}
      </Container>
    </>
  );
};

export { OrdersTable };
