import React, { useState, useEffect } from "react";
import {
  TextField,
  Button,
  Typography,
  Container,
  Box,
  Card,
  CardContent,
} from "@mui/material";
import axios from "api/axios";

import useAuth from "../../hooks/useAuth";
import { useNavigate, useLocation, Link } from "react-router-dom";

import cryptojs from "crypto-js";
import styles from "./Register.module.css";

const Register = () => {
  const [username, setUsername] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");
  const { setAuth, persist, setPersist } = useAuth();
  const navigate = useNavigate();
  const location = useLocation();
  const from = location.state?.from?.pathname;

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError("");

    try {
      const response = await axios.post(
        "api/reguser",
        {
          username,
          email,
          password: cryptojs.MD5(password).toString(),
        },
        {
          headers: { "Content-Type": "application/json" },
          withCredentials: true,
        }
      );
      console.log("User registered:", response.data);
      // Redirect or display success message
      const {
        jwt: accessToken,
        username: firstname,
        api_list,
        code: promocode,
      } = response?.data?.accessToken || {};
      const roles = [1];
      setAuth({
        username,
        promocode,
        email,
        roles,
        accessToken,
        firstname,
        api_list,
      });
      navigate(from || `/`, { replace: true });
    } catch (err) {
      if (err.response) {
        if (err.response.status === 409) {
          setError("Такой пользователь существует");
        } else {
          setError("An error occurred. Please try again later.");
        }
      } else {
        setError("An error occurred. Please try again later.");
      }
    }
  };

  const inputStyle = {
    WebkitBoxShadow: "0 0 0 1000px white inset",
  };

  return (
    <div className={styles.welcome}>
      <div className={styles.loginRegisterContainer}>
        <div className={styles.header}>
          <div className={styles.headerline}>
            <Card sx={{ minWidth: 395, minHeight: 295, marginTop: "25px" }}>
              <CardContent>
                <span className={styles.headerS}>Регистрация</span>

                <form onSubmit={handleSubmit}>
                  <TextField
                    inputProps={{ style: inputStyle }}
                    placeholder="Имя пользователя"
                    value={username}
                    onChange={(e) => setUsername(e.target.value)}
                    fullWidth
                    margin="normal"
                    autoComplete="Имя пользователя"
                  />
                  <TextField
                    inputProps={{ style: inputStyle }}
                    placeholder="Email"
                    type="email"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    fullWidth
                    margin="normal"
                  />
                  <TextField
                    inputProps={{ style: inputStyle }}
                    placeholder="Пароль"
                    type="password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    fullWidth
                    margin="normal"
                    autoComplete="current-password"
                  />
                  {error && <Typography color="error">{error}</Typography>}
                  <Button
                    type="submit"
                    variant="contained"
                    color="primary"
                    sx={{ mt: 2 }}
                  >
                    Зарегистрироваться
                  </Button>
                  <div className={styles.persistcheck}>
                    <Link className={styles.headerS} to={`/login`}>
                      Вход
                    </Link>
                  </div>
                </form>
              </CardContent>
            </Card>
          </div>
        </div>
      </div>
    </div>
  );
};

export { Register };
