import { useState } from 'react';
import { CloseButton } from 'UI/CloseButton';
import styles from './DeleteData.module.css';
import axios from 'api/axios';
import { useNavigate } from 'react-router-dom';
import { TextField, Button, FormControl } from '@mui/material';

const DeleteData = props => {
  function isValidDate(d) {
    if (d === null) {
      d = '';
    }
    d = new Date(d);
    if (d instanceof Date && !isNaN(d)) {
      return d.toLocaleDateString('ru-RU');
    } else return 'Дата не задана';
  }
  const navigate = useNavigate();
  const submitHandler = e => {
    e.preventDefault();
    props.onCancel();
    axios
      .get(
        `incomeblocks/${
          props.todel.t.contr_type ? 'contractsdocdel' : 'contractsgovdel'
        }?recordid=${props.todel.t.contr_id}`
      )
      .catch(error => {
        if (error.response.status === 401) navigate('/login');
      })
      .then(response => {
        console.log(response);
      });
  };
  console.log('todel', props.todel.t);
  return (
    <>
      <div className={styles.backdrop} onClick={props.onCancel} />
      <form className={styles.modal} onSubmit={submitHandler}>
        <div className={styles.formaData}>
          <div className={styles.rowSave}>
            <div className={styles.formaDataList}>
              <div>Вы уверены, что хотите безвозвратно удалить эту запись?</div>
              <div>
                {props?.namedoc} №
                {`${
                  props?.todel?.t?.gov_name ? props?.todel?.t?.gov_name : ''
                }${
                  props?.todel?.t?.contr_name && props?.todel?.t?.gov_name
                    ? '/'
                    : ''
                }${
                  props?.todel?.t?.contr_name ? props?.todel?.t?.contr_name : ''
                } от ${
                  props?.todel?.t?.contr_date
                    ? props?.todel?.t?.contr_date.slice(
                        0,
                        props?.todel?.t?.contr_date.length - 14
                      )
                    : ''
                }`}
              </div>
              <div className={styles.row}>
                <Button
                  sx={{ marginRight: '30px', minWidth: '15ch' }}
                  variant="contained"
                  color="primary"
                  type="submit"
                  value="Удалить"
                >
                  Удалить
                </Button>
                <Button
                  sx={{ marginRight: '30px', minWidth: '15ch' }}
                  variant="contained"
                  color="primary"
                  type="button"
                  value="Сохранить"
                  onClick={props.onCancel}
                >
                  Отменить
                </Button>
              </div>
            </div>
            <CloseButton close={props.onCancel} />
          </div>
        </div>
      </form>
    </>
  );
};

export { DeleteData };
