import { useLocation, Navigate, Outlet, useNavigate } from "react-router-dom";
import axios from "api/axios";
import useAuth from "hooks/useAuth";
import { useEffect, useState } from "react";

const RequireAuth = ({ allowedRoles }) => {
  const navigate = useNavigate();
  const { auth } = useAuth();
  const location = useLocation();

  const extract_link = (text) => {
    const match = text.match(/\/?([^/]+)/);
    return match ? match[1] : "";
  };
  let api = extract_link(location.pathname);

  const get_api = auth?.api_list?.find(
    (api_item) => api_item.api_info.data === api || ""
  );

  console.log(
    "auth?.accessToken",
    auth,
    allowedRoles,
    "location",
    get_api,
    "api",
    api
  );

  if (api !== "") {
    axios
      .get(`/apicheck?api=${api}`, {
        withCredentioal: true,
      })
      .then((response) => {
        // console.log("useEffectapi",response.data);
        console.log("доступ разрещен", response);
      })
      .catch((error) => {
        console.log(error.response.status, "Нет ответа от сервера");
        if (error.response?.status === 403) {
          navigate("forbidden", { replace: true });
        } else if (error.response?.status === 401) {
          navigate("login", { replace: true });
        }
      });
  }

  // setAuth((prev) => {
  //   console.log("prev", JSON.stringify(prev));
  //   console.log("response", response);
  //   return {
  //     ...prev,
  //     roles: [1],
  //     firstname: response.data.refreshToken.username,
  //     accessToken: response.data.refreshToken,
  //     api_list: response?.data?.refreshToken?.api_list,
  //   };
  // });
  // return response.data.refreshToken;

  // return allowedRoles?.includes(1) ? (
  //   <Outlet />
  // ) : auth?.accessToken ? (
  //   <Navigate to="/anauthorized" state={{ from: location }} replace />
  // ) : (
  //   <Navigate to="/login" state={{ from: location }} replace />
  // );

  return auth?.accessToken ? (
    <Outlet />
  ) : (
    <Navigate to="/login" state={{ from: location }} replace />
  );
};

export default RequireAuth;
