import React, { useContext, useState } from "react";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";
import Menu from "@mui/material/Menu";
import MenuIcon from "@mui/icons-material/Menu";
import Container from "@mui/material/Container";
import Tooltip from "@mui/material/Tooltip";
import MenuItem from "@mui/material/MenuItem";
import Assessment from "@mui/icons-material/Assessment";
import CoPresent from "@mui/icons-material/CoPresent";
import { Link, NavLink, Outlet, useNavigate } from "react-router-dom";
import styles from "./NavBar.module.css";
import AuthContext from "context/AuthProvider";
import useLogout from "hooks/useLogout";

function NavBar(props) {
  const { auth } = useContext(AuthContext);
  const navigate = useNavigate();
  const [anchorElNav, setAnchorElNav] = useState(null);
  const [anchorElUser, setAnchorElUser] = useState(null);
  const handleOpenNavMenu = (event) => {
    setAnchorElNav(event.currentTarget);
  };
  const handleOpenUserMenu = (event) => {
    setAnchorElUser(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  const logout = useLogout();

  const signOut = async () => {
    navigate("/login");
    await logout();
  };

  const handleCloseUserMenu = () => {
    setAnchorElUser(null);
  };

  // const pages = [
  //   ["Поиск рекламаций", "/searchfailures"],
  //   ["Добавить рекламацию", "/searchblocks"],
  //   ["Отчеты", "/reports"],
  //   ["Входящие документы", "/specialcheck"],
  // ];

  const pages = props.link;
  // const settings = ["Profile", "Account", "Dashboard", "Logout"];
  console.log("nav", auth);
  return (
    <>
      <AppBar position="relative">
        <Container maxWidth="xl">
          <Toolbar disableGutters>
            <Box sx={{ flexGrow: 1, display: { xs: "flex", md: "flex" } }}>
              <IconButton
                size="large"
                aria-label="account of current user"
                aria-controls="menu-appbar"
                aria-haspopup="true"
                onClick={handleOpenNavMenu}
                color="inherit"
              >
                <MenuIcon />
              </IconButton>
              <Menu
                id="menu-appbar"
                anchorEl={anchorElNav}
                anchorOrigin={{
                  vertical: "bottom",
                  horizontal: "left",
                }}
                keepMounted
                transformOrigin={{
                  vertical: "top",
                  horizontal: "left",
                }}
                open={Boolean(anchorElNav)}
                onClose={handleCloseNavMenu}
                sx={{
                  display: { xs: "block" },
                }}
              >
                {" "}
                {/* <MenuItem key={23}>
                  <Link key={23} to={`/`}>
                    Главная страница
                  </Link>
                </MenuItem> */}
                {auth.api_list.map((el, id) => (
                  <MenuItem key={id}>
                    <Link key={id} to={`/${el?.api_info?.data}`}>
                      {el?.api_info?.description}
                    </Link>
                  </MenuItem>
                ))}
              </Menu>
            </Box>
            <Assessment sx={{ display: { xs: "none", md: "flex" }, mr: 1 }} />
            <Typography
              variant="h6"
              noWrap
              sx={{
                mr: 2,
                display: { xs: "none", md: "flex" },
                fontFamily: "monospace",
                fontWeight: 700,
                letterSpacing: ".3rem",
                color: "inherit",
                textDecoration: "none",
              }}
            >
              {/* <Link key={178} className={styles.link} to="/">
                {props.name}
              </Link> */}
              {props.name}
            </Typography>

            <Assessment sx={{ display: { xs: "flex", md: "none" }, mr: 1 }} />

            <Box sx={{ flexGrow: 1, display: { xs: "none", md: "flex" } }}>
              {pages.map((page, id) => (
                <Typography
                  key={id}
                  sx={{ fontSize: 16, mx: 2 }}
                  textAlign="center"
                >
                  <NavLink
                    key={17}
                    className={(isActive) =>
                      isActive ? styles.link : styles.active
                    }
                    to={page[1]}
                  >
                    {page[0]}
                  </NavLink>
                </Typography>
              ))}
            </Box>

            <Box sx={{ flexGrow: 0, display: "flex" }}>
              <Typography
                key={222}
                sx={{ fontSize: 16, mx: 2 }}
                textAlign="center"
              >
                {auth.firstname}
              </Typography>

              <Tooltip title="Настройки пользователя">
                <IconButton onClick={handleOpenUserMenu} sx={{ p: 0 }}>
                  <CoPresent
                    sx={{ display: { xs: "none", md: "flex" }, mr: 1 }}
                  />
                </IconButton>
              </Tooltip>
              <Menu
                sx={{ mt: "45px" }}
                id="menu-appbar"
                disableScrollLock={true}
                anchorEl={anchorElUser}
                anchorOrigin={{
                  vertical: "top",
                  horizontal: "right",
                }}
                keepMounted
                transformOrigin={{
                  vertical: "top",
                  horizontal: "right",
                }}
                open={Boolean(anchorElUser)}
                onClose={handleCloseUserMenu}
              >
                <MenuItem key={"exit"} onClick={handleCloseUserMenu}>
                  <Typography key={9} textAlign="center" onClick={signOut}>
                    Выход
                  </Typography>
                </MenuItem>
              </Menu>
            </Box>
          </Toolbar>
        </Container>
      </AppBar>
    </>
  );
}

export { NavBar };
