import { useRef, useState, useEffect } from "react";
import useAuth from "../../hooks/useAuth";
import { useNavigate, useLocation, Link } from "react-router-dom";
import axios from "api/axios";
import cryptojs from "crypto-js";
import {
  TextField,
  Button,
  Card,
  CardContent,
  FormControl,
  CircularProgress,
} from "@mui/material";

import styles from "./LogIn.module.css";

const LOGIN_URL = "/auth";

const LogIn = () => {
  const inputStyle = {
    WebkitBoxShadow: "0 0 0 1000px white inset",
  };
  const { setAuth, persist, setPersist } = useAuth();

  const navigate = useNavigate();
  const location = useLocation();
  const from = location.state?.from?.pathname;

  const userRef = useRef();
  const errRef = useRef();

  const [user, setUser] = useState("");
  const [pwd, setPwd] = useState("");
  const [errMsg, setErrMsg] = useState("");
  const [showLaoding, setShowLoading] = useState(false);

  const [msgSign, setMsgSign] = useState("");
  const [handlErrorSignIn, setHandleErrorSignIn] = useState(false);

  useEffect(() => {
    userRef.current.focus();
  }, []);

  useEffect(() => {
    setErrMsg("");
  }, [user, pwd]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setShowLoading(true);
    try {
      if (!user) {
        setHandleErrorSignIn(true);
        setMsgSign("Имя пользователя не может быть пустым");
        setShowLoading(false);
        return;
      } else if (!pwd) {
        setHandleErrorSignIn(true);
        setMsgSign("Пароль не может быть пустым");
        setShowLoading(false);
        return;
      }

      setHandleErrorSignIn(false);
      setMsgSign("");

      const response = await axios.post(
        LOGIN_URL,
        JSON.stringify({ user, pwd: cryptojs.MD5(pwd).toString() }),
        { headers: { "Content-Type": "application/json" } }
      );
      console.log("response?.data", response?.data);
      const {
        jwt: accessToken,
        username: firstname,
        api_list,
        code: promocode,
      } = response?.data?.accessToken || {};
      const roles = [1];

      setAuth({ user, roles, promocode, accessToken, firstname, api_list });
      setUser("");
      setPwd("");
      // navigate(from || `/${api_list[0]?.api_info.data}`, { replace: true });
      navigate(from || `/`, { replace: true });
    } catch (err) {
      if (!err?.response) {
        setHandleErrorSignIn(true);
        setMsgSign("Нет ответа от сервера");
      } else if (err.response?.status === 500) {
        setHandleErrorSignIn(true);
        setMsgSign("Нет ответа от сервера");
      } else if (err.response?.status === 401) {
        setHandleErrorSignIn(true);
        setMsgSign("Вы ввели неправильное имя пользователя или пароль");
      } else {
        setHandleErrorSignIn(true);
        setMsgSign("Нет ответа от сервера");
      }
    } finally {
      setShowLoading(false);
    }
  };
  const togglePersist = () => {
    setPersist((prev) => !prev);
  };

  useEffect(() => {
    localStorage.setItem("persist", persist);
  }, [persist]);

  return (
    <div className={styles.welcome}>
      <div className={styles.loginRegisterContainer}>
        <div className={styles.header}>
          <div className={styles.headerline}>
            <Card sx={{ minWidth: 395, minHeight: 295, marginTop: "25px" }}>
              <CardContent>
                <span className={styles.headerS}>Вход</span>
                <FormControl required error={handlErrorSignIn}>
                  <form onSubmit={handleSubmit}>
                    <TextField
                      margin="normal"
                      fullWidth
                      variant="standard"
                      inputProps={{ style: inputStyle }}
                      type="text"
                      ref={userRef}
                      placeholder="Имя пользователя"
                      onChange={(e) => {
                        setUser(e.target.value);
                      }}
                      value={user}
                      autoComplete="username"
                    />
                    <TextField
                      inputProps={{ style: inputStyle }}
                      margin="normal"
                      fullWidth
                      variant="standard"
                      type="password"
                      onChange={(e) => {
                        setPwd(e.target.value);
                      }}
                      placeholder="Пароль"
                      error={handlErrorSignIn}
                      helperText={msgSign}
                      value={pwd}
                      autoComplete="current-password"
                    />
                    {showLaoding ? (
                      <Button
                        disabled={true}
                        style={{
                          background: "gray",
                          margin: "35px 15px 15px 0",
                          width: "350px",
                          height: "46px",
                        }}
                      >
                        <CircularProgress />
                      </Button>
                    ) : (
                      <Button
                        variant="contained"
                        color="primary"
                        type="submit"
                        style={{
                          margin: "35px 15px 15px 0",
                          width: "350px",
                          height: "46px",
                        }}
                      >
                        Вход
                      </Button>
                    )}
                    <div className={styles.persistcheck}>
                      <input
                        type="checkbox"
                        id="persist"
                        onChange={togglePersist}
                        checked={persist}
                      />
                      <label htmlFor="persist">Входить автоматически</label>
                    </div>
                    <Link to={`/register`}>Регистриция</Link>
                  </form>
                </FormControl>
              </CardContent>
            </Card>
          </div>
        </div>
      </div>
    </div>
  );
};

export { LogIn };
